import Vue from 'vue'
import Router from 'vue-router'
import HelloWorld from '@/components/HelloWorld'


Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      redirect: '/Fire'
    },
    {
      path: '/Aged',
      name: 'Aged',
      // component: Aged
      component: function (resolve) {
        setTimeout(function () {
          require(['../moudles/aged.vue'], resolve)
        }, 1)
      }
    },
    {
      path: '/Fire',
      name: 'Fire',
      // component: Fire
      component: function (resolve) {
        setTimeout(function () {
          require(['../moudles/fire.vue'], resolve)
        }, 1)
      }
    },
    {
      path: '/Food',
      name: 'Food',
      // component: Food
      component: function (resolve) {
        setTimeout(function () {
          require(['../moudles/food.vue'], resolve)
        }, 1)
      }
    },
    {
      path: '/Community',
      name: 'Community',
      // component: Community
      component: function (resolve) {
        setTimeout(function () {
          require(['../moudles/community.vue'], resolve)
        }, 1)
      }
    },
    {
      path: '/Garbage',
      name: 'Garbage',
      // component: Garbage
      component: function (resolve) {
        setTimeout(function () {
          require(['../moudles/garbage.vue'], resolve)
        }, 1)
      }
    },
    {
      path: '/Street',
      name: 'Street',
      // component: Street
      component: function (resolve) {
        setTimeout(function () {
          require(['../moudles/street.vue'], resolve)
        }, 1)
      }
    },
    {
      path: '/Market',
      name: 'Market',
      // component: Market
      component: function (resolve) {
        setTimeout(function () {
          require(['../moudles/Market.vue'], resolve)
        }, 1)
      }
    },
    {
      path: '/Water',
      name: 'Water',
      // component: Water
      component: function (resolve) {
        setTimeout(function () {
          require(['../moudles/Water.vue'], resolve)
        }, 1)
      }
    }

  ]
})



